import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Inject,
  OnDestroy,
  OnInit,
  Optional,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import {
  BB_PAYORD_PAYMENT_ERROR_MESSAGES_TOKEN,
  CounterPartyFields,
  defaultPaymentSuccessEventName,
  FetchPaymentOptionsListenerFn,
  HiddenFields,
  InitiatePaymentHeaderStates,
  InitiatePaymentOptions,
  NavigationService,
  PaymentBaseFields,
  PaymentError,
  PaymentErrors,
  PaymentFormFieldConfig,
  PaymentFormGroup,
  PaymentHooks,
  PaymentHooksOptions,
  PaymentMode,
  PaymentOption,
  PaymentOptionsService,
  PaymentOrderHttpService,
  PaymentState,
  PaymentTemplateFields,
  PaymentTemplatesData,
  PaymentTypeConfig,
  PayordPaymentError,
  removeScheduleFromConfig,
  RoutableModalService,
  P2PLimitErrorKeys,
  setFormDetailsFromResponse,
  setFormDetailsFromService,
  setFrequencyOptionsPerConfig,
  StateService,
  toPostPaymentTemplate,
  toPostRequest,
  triggerPaymentHook,
  focusErrorElement,
  BaseObject,
  RemittanceInfoFields,
  getConfig,
} from '@backbase/payment-orders-ang';
import { BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import { AbstractControl, FormControl, FormGroup } from '@angular/forms';
import {
  catchError,
  distinctUntilChanged,
  filter,
  map,
  switchMap,
  take,
  takeUntil,
  tap,
} from 'rxjs/operators';
import { PaymentTemplate } from '@backbase/data-ang/payment-template';
import { IdentifiedPaymentOrder } from '@backbase/data-ang/payment-order';
import { PaymentOptionsResponse } from '@backbase/data-ang/payment-order-options';
import { Pubsub, PUBSUB } from '@backbase/foundation-ang/web-sdk';
import {
  isEditFormDirty,
  registerFormControl,
} from '@backbase/payord-form-builder-ang';

import {
  mapPaymentOptionRequest,
  normalisePaymentTemplate,
  normalizePaymentType,
} from '../../helpers/payment-options-mappers';
import { CreatePaymentConfigSetupService } from '../../services/create-payment-config-setup.service';
import { CreatePaymentFieldsSetupService } from '../../services/create-payment-fields-setup.service';
import { commonI18n, formI18n } from '../../constants/initiate-payment.i18n';
import { CreatePaymentService } from '../../services/create-payment.service';

@Component({
  selector: 'bb-create-payment-form',
  templateUrl: './create-payment-form.component.html',
  providers: [CreatePaymentFieldsSetupService],
})
export class CreatePaymentFormComponent
  implements OnInit, AfterContentInit, OnDestroy, AfterViewInit
{
  private readonly destroyed$ = new Subject<void>();

  private paymentTypes: string | undefined;
  private paymentTemplate: PaymentTemplate | null = null;
  private shouldResetPaymentOptions = false;
  private formValue = this.stateService.formInstance?.value;

  header: string | undefined;
  readonly paymentTemplateSearch = new FormControl();
  readonly fetchOptions$ = new Subject<FormGroup>();
  readonly submitting$ = new BehaviorSubject<boolean>(false);
  readonly paymentState$ = this.stateService.paymentState$;

  readonly paymentTemplates$: Observable<PaymentTemplatesData> =
    this.paymentOrderHttpService.paymentTemplates$;
  readonly loadingTemplates$ = this.paymentOrderHttpService.loading$;

  readonly fetchingPaymentOptions$ = new BehaviorSubject<boolean>(false);
  readonly paymentOptions$ = this.fieldSetupService.paymentOptions$;
  readonly paymentOptionsError$ = this.fieldSetupService.paymentOptionsError$;

  readonly selectedPaymentConfig$ =
    this.createPaymentService.selectedPaymentConfig$;
  readonly selectedPaymentOption$ =
    this.createPaymentService.selectedPaymentOption$;

  readonly fetchPaymentOptionsListener$: Observable<
    FetchPaymentOptionsListenerFn | undefined
  > = this.createPaymentSetup.fetchPaymentOptionsListener$;
  readonly baseFields$: Observable<
    Array<PaymentFormFieldConfig | PaymentFormGroup>
  > = this.createPaymentSetup.baseFields$.pipe(
    map((baseFields) => {
      this.fieldSetupService.injectData(baseFields);
      return baseFields;
    })
  );

  private editedPaymentDetails: IdentifiedPaymentOrder | undefined;

  options: InitiatePaymentOptions = {};
  configs: PaymentTypeConfig[] = [];

  templateSelectorName?: string;
  isPaymentCopyControl?: AbstractControl;
  templateNameControl = this.stateService.formInstance?.get('templateName');

  errors: { key: string; message: string }[] = [];
  isEditPaymentConfirmationDialogOpen = false;
  enablePaymentTemplateSelector = true;
  editPaymentHasNoChanges = false;
  isErrorTitleDisplayed = true;
  shouldApplyTemplate = false;
  genericError?: string;
  searchValue?: string;
  showApiError = false;
  paymentMode = PaymentMode;

  @ViewChild('outlet', { read: ViewContainerRef, static: true })
  outletRef!: ViewContainerRef;
  @ViewChild('content', { read: TemplateRef, static: true })
  contentRef!: TemplateRef<any>;

  @ViewChild('alertContainer') alertContainer?: ElementRef;

  constructor(
    private readonly paymentOrderHttpService: PaymentOrderHttpService,
    private readonly paymentOptionsService: PaymentOptionsService,
    private readonly createPaymentService: CreatePaymentService,
    private readonly navigationService: NavigationService,
    private readonly stateService: StateService,
    private readonly cdRef: ChangeDetectorRef,
    private readonly createPaymentSetup: CreatePaymentConfigSetupService,
    private readonly fieldSetupService: CreatePaymentFieldsSetupService,
    private readonly formEl: ElementRef,
    @Optional() private readonly modalService: RoutableModalService,
    @Inject(PUBSUB) private readonly eventBus: Pubsub,
    @Optional()
    @Inject(BB_PAYORD_PAYMENT_ERROR_MESSAGES_TOKEN)
    readonly paymentErrors: PayordPaymentError
  ) {}

  ngOnInit(): void {
    combineLatest([
      this.createPaymentSetup.configs$,
      this.createPaymentSetup.options$,
      this.stateService.paymentEditState$,
    ])
      .pipe(distinctUntilChanged(), takeUntil(this.destroyed$))
      .subscribe(([configs, options, editedPaymentDetails]) => {
        this.options = options;
        this.editedPaymentDetails = editedPaymentDetails;
        this.isErrorTitleDisplayed =
          options.isErrorTitleDisplayed ?? this.isErrorTitleDisplayed;
        this.enablePaymentTemplateSelector =
          options.enablePaymentTemplateSelector ??
          this.enablePaymentTemplateSelector;

        if (configs.length === 1) {
          // TODO: find a better solution for this
          this.setArrangementBusFunctions(
            configs[0].options?.closedPaymentBusFns,
            configs[0].businessFunction
          );

          this.onSelectConfig(configs[0]);
        }
        const { isOneOffToRecurrentAllowed, isRecurrentToOneOffAllowed } =
          options;

        this.configs = configs.map((config) => {
          let _config = options.isTemplateMode
            ? removeScheduleFromConfig(config)
            : config;

          if (editedPaymentDetails) {
            _config = setFrequencyOptionsPerConfig(
              config,
              !!editedPaymentDetails.schedule,
              {
                isOneOffToRecurrentAllowed,
                isRecurrentToOneOffAllowed,
              }
            );
          }

          this.fieldSetupService.injectData(_config.fields, _config.options);
          return _config;
        });

        this.setupPaymentTypes(this.configs);

        this.header = this.createPaymentSetup.getHeader(
          InitiatePaymentHeaderStates.FORM
        );
      });

    this.listen();

    if (this.stateService.currentState !== PaymentState.EDIT) {
      this.fieldSetupService.clearPaymentOptions();
      this.shouldResetPaymentOptions = true;
    }

    this.createPaymentService
      .getPaymentOptionsRequest(this.configs)
      .pipe(
        tap(
          (options) => {
            if (!this.shouldResetPaymentOptions && !options?.length) {
              this.fieldSetupService.setPaymentOptionsError(true);
            }
            this.shouldResetPaymentOptions = false;
            this.onPaymentTypeChange(options);
          },
          catchError(() => {
            this.fieldSetupService.setPaymentOptionsError(true);
            return of([]);
          })
        ),
        takeUntil(this.destroyed$)
      )
      .subscribe();

    triggerPaymentHook(
      <PaymentHooksOptions>this.createPaymentSetup.hooks,
      PaymentHooks.onInit
    );
  }

  ngAfterViewInit() {
    this.stateService.paymentTemplateState$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((template) => {
        if (template) {
          this.onSelectPaymentTemplate(template);
        }
      });

    if (this.editedPaymentDetails) {
      this.setEditFormValues(this.editedPaymentDetails);
      this.stateService.setEditPayment(undefined); // This is to prevent overwrite manual edits on return from review screen
    }
  }

  ngAfterContentInit() {
    this.renderView();
  }

  ngOnDestroy() {
    this.destroyed$.next();
    triggerPaymentHook(
      <PaymentHooksOptions>this.createPaymentSetup.hooks,
      PaymentHooks.onDestroy
    );
  }

  private setArrangementBusFunctions(
    closedFunctions: string[] | undefined,
    businessFunction: string | undefined
  ) {
    if (closedFunctions?.length && businessFunction) {
      // TODO: temporary solution, need to find better way do not mutate object
      const setOfBusFun = new Set([...closedFunctions, businessFunction]);
      closedFunctions = Array.from(setOfBusFun);

      this.paymentOrderHttpService.arrangementBusFunctions =
        closedFunctions.toString();
    }
  }

  private setEditFormValues(payment: IdentifiedPaymentOrder) {
    const form: FormGroup = this.stateService.formInstance;
    const counterparty = form.get(PaymentBaseFields.counterparty) as FormGroup;
    registerFormControl(
      counterparty,
      CounterPartyFields.schemeName,
      [],
      [],
      'BBAN'
    );
    registerFormControl(form, 'paymentOrderId', [], [], payment.id);
    this.isPaymentCopyControl = registerFormControl(
      form,
      HiddenFields.isPaymentCopy
    );
    this.isPaymentCopyControl.patchValue(
      this.options.paymentMode === this.paymentMode.COPY_PAYMENT
    );
    const editForm = setFormDetailsFromService(form, payment);
    this.stateService.initialEditFormValue = editForm.value;
    this.setForm(editForm);
  }

  private setupPaymentTypes(configs: PaymentTypeConfig[]) {
    this.paymentTypes = configs.map((config) => config.paymentType).join(',');
    this.paymentOrderHttpService.paymentType = this.paymentTypes;
  }

  private renderView() {
    this.outletRef.clear();
    this.outletRef.createEmbeddedView(this.contentRef);
  }

  private setTemplateValues(form: FormGroup, paymentTemplate: PaymentTemplate) {
    this.setFormValues(normalisePaymentTemplate(paymentTemplate), form);
    this.paymentTemplate = null;
    this.stateService.setPaymentTemplate(undefined);
  }

  private prepareForTemplateApply() {
    const formGroup = new FormGroup({});
    this.formValue = null;
    this.fieldSetupService.setPaymentOptions([]);
    this.templateNameControl = registerFormControl(formGroup, 'templateName');
    this.stateService.saveForm(formGroup);
  }

  private getPaymentConfig(
    paymentType?: string
  ): PaymentTypeConfig | undefined {
    if (paymentType) {
      return this.configs.filter(
        (paymentConfig) => paymentType === paymentConfig.paymentType
      )[0];
    }
    return;
  }

  private setFormValues(
    { details, name, id }: PaymentTemplate,
    form?: FormGroup
  ) {
    const _form = form || this.stateService.formInstance;
    const templateForm = setFormDetailsFromResponse(_form, details, {
      name,
      id,
    });
    this.templateSelectorName = name;
    this.templateNameControl?.setValue(name);
    this.setForm(templateForm);
    _form.markAllAsTouched();
  }

  private listen() {
    this.fetchOptions$
      .pipe(
        filter(
          (form: FormGroup) =>
            JSON.stringify(
              CreatePaymentService.getCriticalFieldValues(this.formValue)
            ) !==
            JSON.stringify(
              CreatePaymentService.getCriticalFieldValues(form.getRawValue())
            )
        ),
        switchMap((form: FormGroup) => {
          this.formValue = form.getRawValue();
          return this.fetchPaymentOptions(form);
        }),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  private setForm(formInstance: FormGroup) {
    this.stateService.setPaymentState(PaymentState.EDIT);
    this.stateService.saveForm(formInstance);

    this.renderView();
    this.cdRef.detectChanges();
  }

  /**
   * Callback triggered when the payment type changes
   */
  private onPaymentTypeChange(paymentOptions: Array<PaymentOption> = []) {
    if (paymentOptions && paymentOptions.length) {
      this.fieldSetupService.setPaymentOptionsError(false);
    }

    this.fieldSetupService.setPaymentOptions(paymentOptions);
    this.fetchingPaymentOptions$.next(false);
  }

  private validatePayment(form: FormGroup) {
    this.paymentOrderHttpService
      .validatePayment(toPostRequest(form.getRawValue()))
      .pipe(take(1))
      .subscribe(
        (response) => {
          const control =
            form.get('isIntraLegalEntity') ||
            registerFormControl(form, 'isIntraLegalEntity');
          this.templateNameControl =
            this.templateNameControl ||
            registerFormControl(form, 'templateName');
          control.setValue(response.isIntraLegalEntityPaymentOrder);
          this.templateNameControl.setValue(this.templateSelectorName);
          this.submitting$.next(false);
          this.navigationService.review();
          this.stateService.canApprove = response.canApprove;
        },
        ({ error }) => {
          this.submitting$.next(false);
          this.errors = error.errors;

          if (!this.isP2PLimitError(error)) {
            this.genericError = error.message;
            this.showApiError = true;
            if (this.errors?.length || this.genericError) {
              const errorObject: PaymentErrors = {
                errorResponse: error,
                errors: this.errors,
                paymentErrors: this.paymentErrors,
                notificationHeader: formI18n.validateFailed,
                genericMessage: this.genericError,
              };
              this.paymentOrderHttpService.manageErrorResponse(errorObject);
            } else {
              this.paymentOrderHttpService.showNotification(false, {
                header: formI18n.unknownErrorHeader,
                message: formI18n.unknownError,
                headerContext: error,
              });
            }
            this.scrollIntoAlertView();
          } else {
            this.showApiError = false;
            this.handleP2PErrors(this.errors[0]);
          }
        }
      );
  }

  private handleP2PErrors(error: BaseObject<any>): void {
    if (error?.key) {
      const amountGroupConfig = getConfig(
        RemittanceInfoFields.amountCurrencyGroup,
        this.configs[0]
      );

      const validationMsg =
        amountGroupConfig?.options?.validationMessages?.find(
          (validationMessage: { name: string; message: string }) =>
            validationMessage.name === error.key
        );

      if (validationMsg?.message) {
        validationMsg.message = validationMsg.message.replace(
          '#amount#',
          error.context?.limit
        );

        const amountGroup = this.stateService.formInstance
          .get(PaymentBaseFields.remittanceInfo)
          ?.get(RemittanceInfoFields.amountCurrencyGroup);
        amountGroup?.setErrors({ [error.key]: true });
        amountGroup
          ?.get(RemittanceInfoFields.amount)
          ?.setErrors({ invalid: true });

        focusErrorElement(
          this.formEl?.nativeElement,
          'small.bb-input-validation-message'
        );
      }
    }
  }

  private scrollIntoAlertView() {
    this.alertContainer?.nativeElement.scrollIntoView(false, {
      behavior: 'smooth',
      block: 'start',
    });
  }

  private isP2PLimitError(error: any): boolean {
    if (!Array.isArray(error.errors)) return false;

    const errKey = error.errors[0]?.key;
    return Object.values(P2PLimitErrorKeys).includes(
      errKey as P2PLimitErrorKeys
    );
  }

  private saveTemplate(form: FormGroup) {
    if (this.options.paymentMode === PaymentMode.EDIT_TEMPLATE) {
      this.editPaymentTemplate(form);
    } else {
      this.createPaymentTemplate(form);
    }
  }

  private editPaymentTemplate(form: FormGroup) {
    const templateName = form.get(PaymentTemplateFields.templateName)?.value;
    const paymentTemplateId = form.get(
      PaymentTemplateFields.paymentTemplateId
    )?.value;
    if (paymentTemplateId) {
      this.paymentOrderHttpService
        .editPaymentTemplate(
          paymentTemplateId,
          toPostPaymentTemplate(form.getRawValue())
        )
        .pipe(takeUntil(this.destroyed$))
        .subscribe(
          this.saveTemplateSuccessCallback.bind(this, templateName),
          this.saveTemplateErrorCallback.bind(this)
        );
    }
  }

  private createPaymentTemplate(form: FormGroup) {
    const templateName = form.get(PaymentTemplateFields.templateName)?.value;
    this.paymentOrderHttpService
      .postPaymentTemplate(toPostPaymentTemplate(form.getRawValue()))
      .pipe(takeUntil(this.destroyed$))
      .subscribe(
        this.saveTemplateSuccessCallback.bind(this, templateName),
        this.saveTemplateErrorCallback.bind(this)
      );
  }

  private saveTemplateSuccessCallback(templateName: string) {
    this.paymentOrderHttpService.showNotification(true, {
      header: commonI18n.templateSaveSuccess(templateName),
      headerContext: { name: templateName },
    });
    this.submitting$.next(false);
    if (this.options.isModalView) {
      this.modalService?.closeModal();
    } else {
      this.stateService.resetForm();
      this.renderView();
    }
    this.eventBus.publish(
      <string>(this.options.successEventName || defaultPaymentSuccessEventName),
      true
    );
  }

  private saveTemplateErrorCallback(response: PaymentError) {
    const { error } = response;
    this.paymentOrderHttpService.showNotification(false, {
      header: error?.message || commonI18n.templateSaveFailed,
    });
    this.submitting$.next(false);
  }

  private onHooksDone(form: FormGroup, success = true) {
    if (success) {
      if (this.options.isTemplateMode) {
        this.saveTemplate(form);
      } else {
        this.validatePayment(form);
      }
    } else {
      this.submitting$.next(false);
    }
  }

  private isEditFormNotChanged(form: FormGroup): boolean {
    return !isEditFormDirty(form.value, this.stateService.initialEditFormValue);
  }

  onSelectPaymentTemplate(paymentTemplateData: PaymentTemplate) {
    const config = this.getPaymentConfig(
      normalizePaymentType(paymentTemplateData)
    );
    if (this.configs.length > 1 && config) {
      this.prepareForTemplateApply();
      this.paymentTemplate = normalisePaymentTemplate(paymentTemplateData);
      this.shouldApplyTemplate = true;
      this.stateService.setPaymentState(PaymentState.EDIT);
      this.onSelectConfig(config);
      this.renderView();
    } else {
      this.setFormValues(paymentTemplateData);
    }
  }

  clearTemplateName() {
    this.templateSelectorName = '';
    if (this.templateNameControl) {
      this.templateNameControl.setValue('');
    }
  }

  onLoadingMoreTemplates(page: number) {
    this.paymentOrderHttpService.loadMore(page);
  }

  search(text: string) {
    this.paymentOrderHttpService.search(text);
  }

  onSelectConfig(config?: PaymentTypeConfig) {
    this.createPaymentService.selectPaymentConfig(config);
  }

  onPaymentOptionSelect(paymentOption?: PaymentOption) {
    this.createPaymentService.selectPaymentOption(paymentOption);
  }

  onApplyTemplate(form: FormGroup) {
    this.shouldApplyTemplate = false;
    if (this.paymentTemplate) {
      this.setTemplateValues(form, this.paymentTemplate);
    } else {
      this.stateService.paymentTemplateState$
        .pipe(takeUntil(this.destroyed$))
        .subscribe((template) => {
          if (template) {
            this.setTemplateValues(form, template);
          }
        });
    }
  }

  /**
   * Fetches the payment options for provided payment details
   */

  private fetchPaymentOptions(
    form: FormGroup
  ): Observable<PaymentOptionsResponse> {
    this.fetchingPaymentOptions$.next(true);
    return this.paymentOptionsService.fetchPaymentOptions(
      mapPaymentOptionRequest(form.getRawValue())
    );
  }

  discardEditPaymentChanges() {
    this.modalService?.closeModal();
  }

  cancelEditPaymentConfirmationDialog() {
    this.isEditPaymentConfirmationDialogOpen = false;
  }

  onSubmit(form: FormGroup) {
    const configVal = this.createPaymentService.selectedPaymentConfigValue;

    if (configVal) {
      this.createPaymentService.resetRedundantFields(configVal, form);
    }

    this.stateService.saveForm(form);
    this.submitting$.next(true);
    if (this.createPaymentSetup.hooks?.[PaymentHooks.onSave]) {
      triggerPaymentHook(
        this.createPaymentSetup.hooks,
        PaymentHooks.onSave,
        this.onHooksDone.bind(this, form),
        form
      );
    } else {
      this.onHooksDone(form);
    }
  }

  onClear() {
    this.fieldSetupService.clearPaymentOptions();
    this.stateService.setPaymentState(PaymentState.INITIAL);
    this.stateService.resetForm();
    this.clearTemplateName();
    this.formValue = null;
    this.searchValue = '';
    this.showApiError = false;
    this.renderView();
  }

  onEditCancel(form: FormGroup) {
    if (this.isEditFormNotChanged(form)) {
      this.modalService?.closeModal();
    } else {
      this.isEditPaymentConfirmationDialogOpen = true;
    }
  }

  onEditSubmit(form: FormGroup) {
    if (this.isEditFormNotChanged(form)) {
      this.editPaymentHasNoChanges = true;
      this.scrollIntoAlertView();
    } else {
      this.editPaymentHasNoChanges = false;
      this.onSubmit(form);
    }
  }

  getForm(paymentState: PaymentState): Observable<FormGroup | undefined> {
    if (paymentState === PaymentState.REVIEW) {
      this.stateService.resetForm();
      this.stateService.setPaymentState(PaymentState.INITIAL);
    }

    return this.stateService.form$;
  }
}
