import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Inject,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import {
  PaymentsHeaderNavigationItem,
  PaymentsHeaderNavigationItemWithPermission,
} from './models/payments-header.models';
import { Placement } from '@ng-bootstrap/ng-bootstrap';
import { CONDITIONS, PropertyResolver } from '@backbase/foundation-ang/web-sdk';
import { from, Observable, of } from 'rxjs';
import { concatMap, map, reduce } from 'rxjs/operators';

@Component({
  selector: 'bb-payments-header',
  templateUrl: './payments-header.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentsHeaderComponent implements OnInit {
  /**
   * Custom class name
   */
  @Input() customClassName = '';

  /**
   * Heading Classes
   */
  @Input() headingClasses = '';

  /**
   * Heading type (h1-h6)
   */
  @Input() headingType = '';

  /**
   * Heading
   */
  @Input() heading = '';

  /**
   * Show right side button / dropdown
   */
  @Input() showNavigation = true;

  /**
   * Array of representation of the link menu object
   */
  @Input() navigationItems: PaymentsHeaderNavigationItem<any>[] = [];

  /**
   * Flag to denote if the first link from buttons array should be used as default target
   */
  @Input() useFirstLinkAsDefault = false;

  /**
   * Flag to denote if the first link from buttons array should be used as default target
   */
  @Input() position: Placement = 'bottom-right';

  /**
   * Button label. Will be used if useFirstLinkAsDefault = false;
   */
  @Input() buttonLabel!: string;

  /**
   * Button icon. Will be used if useFirstLinkAsDefault = false;
   */
  @Input() buttonIcon!: string;

  /**
   * Color of the button. Default `primary`
   */
  @Input() buttonColor = 'primary';

  /**
   * Size of the button. Allowed values are `sm` or `md`. Default `md`
   */
  @Input() buttonSize: 'sm' | 'md' = 'md';

  /**
   * Event on item click
   */
  @Output() navigationAction = new EventEmitter<any>();

  menuItems!: Observable<PaymentsHeaderNavigationItem<any>[]>;

  private hasPermissions(
    item: PaymentsHeaderNavigationItem<any>
  ): Observable<PaymentsHeaderNavigationItemWithPermission<any>> {
    const hasPermissionsPromise = item.canView
      ? this.conditions.resolveEntitlements(item.canView)
      : of(true);
    return from(hasPermissionsPromise).pipe(
      map((hasPermissions) => ({ hasPermissions, ...item }))
    );
  }

  private menuSetup(): Observable<PaymentsHeaderNavigationItem<any>[]> {
    return from(this.navigationItems).pipe(
      concatMap(this.hasPermissions.bind(this)),
      reduce((acc, { hasPermissions, ...item }) => {
        if (hasPermissions) {
          acc.push({ ...item });
        }
        return acc;
      }, [] as PaymentsHeaderNavigationItem<any>[])
    );
  }

  constructor(
    @Inject(CONDITIONS) private readonly conditions: PropertyResolver
  ) {}

  ngOnInit(): void {
    this.menuItems = this.menuSetup();
  }

  onClick(value: any) {
    this.navigationAction.emit(value);
  }
}
