import { Injectable, OnDestroy } from '@angular/core';
import {
  accMap,
  BeneficiaryData,
  BeneficiaryList,
  FetchCreditorItemsParams,
  handleCurrenciesList,
  InitiatorFields,
  mapArrangementList,
  mapConnectedAccounts,
  PaymentComponents,
  InitiatePaymentOptions,
  PaymentFormFieldConfig,
  PaymentFormFieldOptions,
  PaymentFormGroup,
  PaymentOption,
  PaymentOptionsService,
  PaymentOrderHttpService,
  PaymentTypeConfig,
  PaymentTypeOptions,
  ScheduleFields,
  shouldLoadTabContent,
  ContactsAndArrangementsGetParams,
  PaymentMode,
  CounterPartyFields,
  OutputItem,
} from '@backbase/payment-orders-ang';
import { BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import {
  catchError,
  filter,
  map,
  shareReplay,
  switchMap,
  takeUntil,
  tap,
} from 'rxjs/operators';
import { RateGet } from '@backbase/data-ang/payment-order';
import { HttpErrorResponse } from '@angular/common/http';

import { CreatePaymentConfigSetupService } from './create-payment-config-setup.service';
import {
  ArrangementItem,
  ArrangementList,
  ContactList,
  ExternalAccountFormItem,
  InternalAccountFormItem,
} from '@backbase/payment-orders-ang';

@Injectable()
export class CreatePaymentFieldsSetupService implements OnDestroy {
  private readonly destroyed$ = new Subject<void>();
  private readonly fetchDebitItemPermissions$ = new Subject<string>();
  private readonly fetchBeneficiaries$ =
    new Subject<FetchCreditorItemsParams>();

  private readonly paymentOptionsSub$ = new BehaviorSubject<PaymentOption[]>(
    []
  );
  private readonly paymentOptionsErrorSub$ = new BehaviorSubject<boolean>(
    false
  );

  private readonly exchangeRate$ = new Subject<number>();
  private readonly getExchangeRate$ = new Subject<{
    fromCurrency: string;
    toCurrency: string;
  }>();
  private readonly exchangeRateError$ = new Subject<boolean>();
  private readonly frequencyFieldSub$ = new Subject<PaymentFormFieldConfig>();

  private readonly countries$ = this.loadSanctionedCountries();
  private readonly currencies$ = this.loadSanctionedCurrencies();

  private get debitAccounts$(): Observable<Array<OutputItem>> {
    return this.paymentOrderHttpService
      .getDebitAccounts({ size: this.options.pageSize })
      .pipe(accMap(this.options.defaultScheme));
  }

  private get creditAccountList$() {
    return this.paymentOrderHttpService
      .getCreditAccounts({ size: this.options.pageSize })
      .pipe(
        map((arrangements) => ({
          items: mapArrangementList(
            arrangements as ArrangementList,
            this.options.defaultScheme
          ),
          count: (arrangements as ArrangementList).count,
        }))
      );
  }

  private readonly fetchDebitAccounts$ =
    new Subject<ContactsAndArrangementsGetParams>();
  private readonly fetchCreditAccounts$ =
    new Subject<ContactsAndArrangementsGetParams>();

  private readonly connectedAccounts$ =
    this.paymentOrderHttpService.connectedAccounts$.pipe(
      map((accounts) => accounts?.map(mapConnectedAccounts))
    );

  private totalContacts = 0; // x-total-count from response header
  private totalCreditAccounts = 0;
  private totalContactsItemsCount = 0; // total count of items in response before extracting sub accounts
  private checkClosedPaymentsAccess = false;

  private contacts$!: Observable<ExternalAccountFormItem[] | HttpErrorResponse>;
  private creditAccounts$!: Observable<
    InternalAccountFormItem[] | HttpErrorResponse
  >;

  readonly paymentOptions$ = this.paymentOptionsSub$.asObservable();
  readonly paymentOptionsError$ = this.paymentOptionsErrorSub$.asObservable();
  readonly frequencyField$ = this.frequencyFieldSub$.asObservable();

  get options(): InitiatePaymentOptions {
    return this.createPaymentSetup.options;
  }

  get configs(): PaymentTypeConfig[] {
    return this.createPaymentSetup.configs;
  }

  constructor(
    private readonly createPaymentSetup: CreatePaymentConfigSetupService,
    private readonly paymentOptionsService: PaymentOptionsService,
    private readonly paymentOrderHttpService: PaymentOrderHttpService
  ) {
    this.fetchDebitItemPermissions$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((dataItemId) => {
        this.paymentOrderHttpService.loadDataItemPermissions(dataItemId);
      });

    this.fetchBeneficiaries$
      .pipe(takeUntil(this.destroyed$))
      .subscribe(({ params, type }: FetchCreditorItemsParams) => {
        this.paymentOrderHttpService.fetchBeneficiaries(
          { ...params, size: this.options.pageSize },
          type
        );
      });

    this.getExchangeRate$
      .pipe(
        filter(({ fromCurrency, toCurrency }) => {
          const isExchangeValid = fromCurrency !== toCurrency;
          if (!isExchangeValid) {
            // If fromCurrency and toCurrency are same, don't fetch exchange rate
            this.exchangeRate$.next(0);
          }
          return isExchangeValid;
        }),
        switchMap(({ fromCurrency, toCurrency }) =>
          this.paymentOrderHttpService.getCurrencyExchangeRate(
            fromCurrency,
            toCurrency
          )
        ),
        tap((exchangeRate: RateGet | null) => {
          this.exchangeRateError$.next(!exchangeRate);
          this.exchangeRate$.next(exchangeRate?.rate || 0);
        }),
        takeUntil(this.destroyed$)
      )
      .subscribe();

    this.fetchDebitAccounts$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((params) => {
        this.paymentOrderHttpService.loadMoreDebitAccounts({
          ...params,
          merge: true,
          size: this.options.pageSize,
        });
      });

    this.fetchCreditAccounts$
      .pipe(takeUntil(this.destroyed$))
      .subscribe(({ from, ...params }) => {
        this.paymentOrderHttpService.loadMoreCreditAccounts(from, {
          ...params,
          merge: true,
          size: this.options.pageSize,
        });
      });
  }

  ngOnDestroy() {
    this.destroyed$.next();
  }

  private loadSanctionedCountries() {
    return this.paymentOptionsService
      .getSanctionedCountries(this.options?.defaultCountry)
      .pipe(
        shareReplay(1),
        catchError((_) => {
          this.paymentOrderHttpService.showNotification(false, {
            header: $localize`:omni-payment-get-countries-failed:Failed to load countries list. Please try again later.`,
          });
          return of([]);
        })
      );
  }

  private loadSanctionedCurrencies(): Observable<{
    currencies?: Array<string>;
    dividerIndex?: number;
  }> {
    return this.paymentOptionsService
      .getSanctionedCurrencies(this.options?.defaultCountry)
      .pipe(
        map((response) => {
          const result: { currencies?: Array<string>; dividerIndex?: number } =
            {};
          if ('labeled' in response) {
            const { currencies, dividerIndex } = handleCurrenciesList(response);

            result.currencies = currencies;
            result.dividerIndex = dividerIndex;
          }
          return result;
        }),
        shareReplay(1),
        catchError((err) => {
          this.paymentOrderHttpService.showNotification(false, {
            header: $localize`:@omni-payment-get-countries-failed:Failed to load currencies list. Please try again later.`,
          });
          return of(err);
        })
      );
  }

  private loadAccountByAccountId(
    id: string,
    accountNumber: string
  ): Observable<InternalAccountFormItem | undefined> {
    return this.paymentOrderHttpService
      .getAccountByAccountId(id, accountNumber)
      .pipe(
        map(
          (item: ArrangementItem): ArrangementList => ({
            count: 1,
            items: [item as ArrangementItem],
          })
        ),
        accMap(this.options.defaultScheme),
        map(
          (list: Array<InternalAccountFormItem>): InternalAccountFormItem =>
            list[0]
        ),
        catchError(() => of(undefined))
      );
  }

  private getContacts(): Observable<
    ExternalAccountFormItem[] | HttpErrorResponse
  > {
    return this.paymentOrderHttpService
      .getContacts({ size: this.options.pageSize, query: '' })
      .pipe(
        tap((data: ContactList | HttpErrorResponse) => {
          if (!(data instanceof HttpErrorResponse)) {
            this.totalContacts = data.count;
            this.totalContactsItemsCount =
              data.itemsCount && data.isLoadMore
                ? this.totalContactsItemsCount + data.itemsCount
                : data.itemsCount || 0;
          } else {
            this.totalContacts = 0;
          }
        }),
        map((value: ContactList | HttpErrorResponse) =>
          !(value instanceof HttpErrorResponse) ? value.items : value
        )
      );
  }

  private getCreditAccounts(): Observable<
    InternalAccountFormItem[] | HttpErrorResponse
  > {
    return this.paymentOrderHttpService
      .getCreditAccounts({ size: this.options.pageSize, searchTerm: '' })
      .pipe(
        tap((data: ArrangementList | HttpErrorResponse) => {
          this.totalCreditAccounts = !(data instanceof HttpErrorResponse)
            ? data.count
            : 0;
        }),
        map((value) =>
          !(value instanceof HttpErrorResponse)
            ? mapArrangementList(value, this.options.defaultScheme)
            : value
        )
      );
  }

  private getDebitAccounts(): Observable<ArrangementList> {
    return this.paymentOrderHttpService
      .getDebitAccounts({ size: this.options.pageSize, searchTerm: '' })
      .pipe(
        map((arrangements) => ({
          items: mapArrangementList(arrangements, this.options.defaultScheme),
          count: arrangements.count,
        }))
      );
  }

  private loadCreditorDetails(
    options: PaymentFormFieldOptions
  ): Observable<BeneficiaryData> {
    this.contacts$ = shouldLoadTabContent(BeneficiaryList.CONTACTS, options)
      ? this.getContacts()
      : of([]);
    this.creditAccounts$ = shouldLoadTabContent(
      BeneficiaryList.ACCOUNTS,
      options
    )
      ? this.getCreditAccounts()
      : of([]);

    return combineLatest([this.creditAccounts$, this.contacts$]).pipe(
      map(([accounts, contacts]) => ({
        accounts,
        contacts,
        totalCreditAccounts: this.totalCreditAccounts,
        totalContacts: this.totalContacts,
        totalContactsItemsCount: this.totalContactsItemsCount,
      }))
    );
  }

  setPaymentOptions(options: PaymentOption[]) {
    this.paymentOptionsSub$.next(options);
  }

  setPaymentOptionsError(error: boolean) {
    this.paymentOptionsErrorSub$.next(error);
  }

  clearPaymentOptions() {
    this.paymentOptionsService.resetPaymentOptions();
    this.setPaymentOptionsError(false);
  }

  injectData(
    fields: Array<PaymentFormGroup | PaymentFormFieldConfig>,
    paymentTypeOptions?: PaymentTypeOptions
  ) {
    fields.forEach((field) => {
      field.options = field.options || {};
      field.options.isTemplateMode = this.options.isTemplateMode;
      field.options.templateModeType = this.options.templateModeType;
      field.options.isModalView = this.options.isModalView;
      field.options.checkClosedPaymentsAccess =
        this.options.checkClosedPaymentsAccess ??
        this.checkClosedPaymentsAccess;
      field.options.isWizardMode = this.configs?.length > 1;

      if ((<PaymentFormGroup>field).fields instanceof Array) {
        this.injectData((<PaymentFormGroup>field).fields, paymentTypeOptions);
      } else {
        switch ((field as PaymentFormFieldConfig).type) {
          case PaymentComponents.debitAccountSelector:
            field.options.items$ = this.getDebitAccounts();
            field.options.fetchItems$ = this.fetchDebitAccounts$;
            field.options.loadAccountByAccountId =
              this.loadAccountByAccountId.bind(this);
            field.options.fetchArrangementPermissions$ =
              this.fetchDebitItemPermissions$;
            field.options.arrangementPermissions$ =
              this.paymentOrderHttpService.arrangementUserPermissions$;
            field.options.checkClosedPaymentsAccess =
              this.options.checkClosedPaymentsAccess;
            break;
          case PaymentComponents.beneficiarySelector:
            field.options.items$ = this.loadCreditorDetails(field.options);
            field.options.fetchItems$ = this.fetchBeneficiaries$;
            break;
          case PaymentComponents.internalAccountSelector:
            field.options.paymentTypeOptions = paymentTypeOptions;
            field.options.connectedItems$ = this.connectedAccounts$;
            field.options.items$ =
              field.name === InitiatorFields.initiatorAccountGroup
                ? this.debitAccounts$
                : this.getCreditAccounts();
            break;
          case PaymentComponents.accountSelector:
            field.options.paymentTypeOptions = paymentTypeOptions;
            field.options.connectedItems$ = this.connectedAccounts$;
            if (
              field.name === CounterPartyFields.counterPartyAccountGroup ||
              this.options.isDebitPayment
            ) {
              field.options.fetchItems$ = this.fetchCreditAccounts$;
              field.options.items$ = this.creditAccountList$;
            } else {
              field.options.items$ = this.getDebitAccounts();
              field.options.fetchItems$ = this.fetchDebitAccounts$;
            }
            break;
          case PaymentComponents.contactSelector:
            field.options.items$ = this.getContacts();
            field.options.fetchItems$ = this.fetchBeneficiaries$;
            break;
          case PaymentComponents.selectPaymentOption:
            field.options.items$ = this.paymentOptions$;
            break;
          case PaymentComponents.sanctionedCountrySelector:
          case PaymentComponents.address:
            field.options.items$ = this.countries$;
            break;
          case PaymentComponents.sanctionedCurrencyAmount:
            field.options.items$ = this.currencies$;
            field.options.getExchangeRate$ = this.getExchangeRate$;
            field.options.exchangeRate$ = this.exchangeRate$;
            field.options.exchangeRateError$ = this.exchangeRateError$;
            break;
          case PaymentComponents.select:
            if (
              field.name === ScheduleFields.frequency &&
              this.options?.paymentMode === PaymentMode.EDIT_PAYMENT
            ) {
              this.frequencyFieldSub$.next(field as PaymentFormFieldConfig);
            }
            break;
        }
      }
    });
  }
}
