import { Injectable } from '@angular/core';
import {
  mapPaymentOptions,
  PaymentDetails,
  PaymentOption,
  PaymentOptionsService,
  PaymentTypeConfig,
} from '@backbase/payment-orders-ang';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { distinctUntilChanged, filter, map } from 'rxjs/operators';
import { PaymentOption as PaymentOptionResponse } from '@backbase/data-ang/payment-order-options';
import { FormGroup } from '@angular/forms';
import {
  resetBankDetails,
  resetBeneficiaryAddress,
} from '../helpers/payment-options-mappers';
import { CriticalFormValues } from '../models/create-payment.model';

@Injectable()
export class CreatePaymentService {
  private readonly selectedPaymentOptionSub$ = new BehaviorSubject<
    PaymentOption | undefined
  >(undefined);
  private readonly selectedPaymentConfigSub$ = new BehaviorSubject<
    PaymentTypeConfig | undefined
  >(undefined);

  selectedPaymentOption$ = this.selectedPaymentOptionSub$
    .asObservable()
    .pipe(distinctUntilChanged());
  selectedPaymentConfig$ = this.selectedPaymentConfigSub$
    .asObservable()
    .pipe(distinctUntilChanged());

  get selectedPaymentConfigValue(): PaymentTypeConfig | undefined {
    return this.selectedPaymentConfigSub$.value;
  }

  static getCriticalFieldValues(
    formValue: Partial<PaymentDetails> | null
  ): CriticalFormValues {
    const { initiator, counterparty, remittanceInfo } = formValue || {};
    return {
      debitAccountNumber: initiator?.accountNumber,
      creditAccountNumber: counterparty?.accountNumber,
      amount: remittanceInfo?.amountGroup,
      country: counterparty?.creditorBank?.postalAddress?.country,
    };
  }

  constructor(private readonly paymentOptionsService: PaymentOptionsService) {}

  selectPaymentConfig(config?: PaymentTypeConfig) {
    this.selectedPaymentConfigSub$.next(config);
  }

  selectPaymentOption(paymentOption?: PaymentOption) {
    this.selectedPaymentOptionSub$.next(paymentOption);
  }

  getPaymentOptionsRequest(
    configs: PaymentTypeConfig[]
  ): Observable<PaymentOption[] | undefined> {
    let optionsReq;

    if (configs.length === 1) {
      const config = configs[0];

      // TODO: find a better solution for this
      if (config.paymentType !== 'INTERNATIONAL_TRANSFER') {
        return EMPTY;
      }

      optionsReq = this.paymentOptionsService
        .getPaymentOptionsRecordByPaymentType(config.paymentType)
        .pipe(
          map((response: PaymentOptionResponse[]) =>
            mapPaymentOptions(response, { [config.paymentType]: config.name })
          )
        );
    } else {
      optionsReq = this.paymentOptionsService.paymentOptions$.pipe(
        filter((options) => !!options),
        map((options?: PaymentOptionResponse[]) =>
          mapPaymentOptions(options || [])
        )
      );
    }

    return optionsReq;
  }

  resetRedundantFields(paymentTypeConfig: PaymentTypeConfig, form: FormGroup) {
    resetBeneficiaryAddress(paymentTypeConfig, form);
    resetBankDetails(paymentTypeConfig, form);
  }
}
