import { Component, ElementRef, Inject, OnDestroy, OnInit, Optional, TemplateRef, ViewChild } from '@angular/core';
import {
  ApprovalStatus,
  BB_PAYORD_PAYMENT_ERROR_MESSAGES_TOKEN,
  BreachReportError,
  contactItemToService,
  defaultPaymentSuccessEventName,
  ForexDetails,
  getForexDetailsFromPayment,
  getLabelFromPaymentConfig,
  InitiatePaymentHeaderStates,
  InitiatePaymentOptions,
  isObjectTruthy,
  ModalState,
  NavigationService,
  noWhitespaceValidator,
  PaymentDetails,
  PaymentError,
  PaymentErrors,
  PaymentHooks,
  PaymentHooksOptions,
  PaymentMode,
  PaymentOrderHttpService,
  PaymentOrderResponseItem,
  PaymentState,
  PaymentSubmitStatuses,
  PaymentTypeConfig,
  PayordPaymentError,
  ProgressStatus,
  ReviewScreens,
  ReviewTemplateContext,
  RoutableModalService,
  StateService,
  Status,
  toPostRequest,
  triggerPaymentHook,
} from '@backbase/payment-orders-ang';

import { catchError, filter, map, pluck, takeUntil, tap } from 'rxjs/operators';
import { BehaviorSubject, EMPTY, Observable, of, Subject } from 'rxjs';
import {
  PaymentOrderGetResponse,
  PaymentOrderProgressStatusGet,
  PaymentOrdersPostResponse,
  PaymentOrdersValidatePost,
} from '@backbase/data-ang/payment-order';
import { HttpErrorResponse } from '@angular/common/http';
import { Pubsub, PUBSUB } from '@backbase/foundation-ang/web-sdk';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TransactionCallbackParam, TransactionSigningHelperService } from '@backbase/identity-auth/transaction-signing';
import { ActiveContactStatus, ContactsPostResponseBody } from '@backbase/data-ang/contact-manager';
import { PaymentTemplatesPostRequest } from '@backbase/data-ang/payment-template';
import { PurposeOfPayment } from '@backbase/data-ang/payment-order-options';

import { CreatePaymentService } from '../../services/create-payment.service';
import { CreatePaymentConfigSetupService } from '../../services/create-payment-config-setup.service';
import { commonI18n, reviewI18n } from '../../constants/initiate-payment.i18n';
import { CapabilityData, P2PLimitReasonCodes } from '../../models/create-payment.model';

@Component({
  selector: 'bb-review-payment',
  templateUrl: 'review-payment.component.html',
})
export class ReviewPaymentContainerComponent implements OnInit, OnDestroy {
  private showConfirmation = false;
  private paymentItem$!: Observable<PaymentDetails>;
  private gc$ = new Subject();
  private saveContactResponse$ = new BehaviorSubject<ContactsPostResponseBody | null>(null);
  private paymentOrderId = '';
  private paymentMessage?: string;
  private approvalPending = false;
  private enablePaymentTemplate = true;
  private saveTemplate = false;
  private canApprove = false;
  private paymentForm?: FormGroup;
  private paymentResponse?: Partial<PaymentOrderResponseItem>;

  @ViewChild('alertContainer') alertContainer!: ElementRef;
  header: string | undefined;
  genericError?: string;
  showBreachDetails = false;
  paymentMode = PaymentMode;
  paymentItem!: PaymentDetails;
  isErrorTitleDisplayed = true;
  reviewScreens = ReviewScreens;
  breachError?: BreachReportError;
  paymentTemplateForm!: FormGroup;
  widgetOptions: InitiatePaymentOptions = {};
  errors: { key: string; message: string }[] = [];
  submitting$ = new BehaviorSubject<boolean>(false);
  savingTemplate$ = new BehaviorSubject<boolean>(false);
  reviewTemplate: TemplateRef<ReviewTemplateContext> | undefined;
  paymentStatus$ = new BehaviorSubject<string | undefined>(undefined);
  selectedPaymentConfig$: Observable<PaymentTypeConfig | undefined> = this.createPaymentService.selectedPaymentConfig$;
  paymentTemplateModalState: BehaviorSubject<ModalState | undefined> = new BehaviorSubject<ModalState | undefined>(
    undefined,
  );

  methods = {
    edit: this.onEdit.bind(this),
    submit: this.submitPayment.bind(this),
    reset: this.resetPayment.bind(this),
    discard: this.discardPayment.bind(this),
    toggleDiscardDialog: this.toggleConfirmationDialog.bind(this),
    showDiscardDialog: this.showDiscardDialog.bind(this),
    isApprovalEnabled: this.isApprovalEnabled.bind(this),
    isPaymentTemplateEnabled: this.isPaymentTemplateEnabled.bind(this),
    isNewPaymentEnabled: this.isNewPaymentEnabled.bind(this),
    isSavingTemplate$: this.isSavingTemplate$.bind(this),
    getPaymentMessage: this.getPaymentMessage.bind(this),
    getPurposeDescription: this.getPurposeDescription.bind(this),
    isSubmitting$: this.isSubmitting$.bind(this),
    getLabelText: getLabelFromPaymentConfig,
    getForexDetails: this.getForexDetails.bind(this),
    showBreachModal: this.showBreachModal.bind(this),
    isTemplateSaved: this.isTemplateSaved.bind(this),
    openTemplateModal: this.openTemplateModal.bind(this),
    doneEditing: this.doneEditing.bind(this),
    navigateToAfterSuccessPage: this.navigateToAfterSuccessPage.bind(this),
    getPaymentResponse: this.getPaymentResponse.bind(this),
    getSaveContactResponse$: this.saveContactResponse$,
    getPaymentStatusType: this.getPaymentStatusType.bind(this),
  };

  createTemplatePermission$ = this.paymentOrderHttpService.createPermission$.pipe(
    map(() => true),
    catchError((error) => of(false)),
  );

  constructor(
    private readonly formBuilder: FormBuilder,
    private readonly stateService: StateService,
    @Inject(PUBSUB) private readonly eventBus: Pubsub,
    private readonly navigationService: NavigationService,
    private readonly createPaymentService: CreatePaymentService,
    @Optional() private readonly modalService: RoutableModalService,
    private readonly paymentOrderHttpService: PaymentOrderHttpService,
    private readonly createPaymentSetup: CreatePaymentConfigSetupService,
    @Optional()
    private readonly transactionSigningHelper: TransactionSigningHelperService,
    @Optional()
    @Inject(BB_PAYORD_PAYMENT_ERROR_MESSAGES_TOKEN)
    readonly paymentErrors: PayordPaymentError,
  ) {}

  onEdit(): void {
    this.stateService.setPaymentState(PaymentState.EDIT);
    this.navigationService.form();
  }

  ngOnInit(): void {
    this.stateService.setPaymentState(PaymentState.REVIEW);
    this.canApprove = this.stateService.canApprove;

    this.createPaymentSetup.reviewTemplate$
      .pipe(
        takeUntil(this.gc$),
        filter((template) => !!template),
      )
      .subscribe((template) => (this.reviewTemplate = template));

    this.createPaymentSetup.options$.pipe(takeUntil(this.gc$)).subscribe((options) => {
      this.isErrorTitleDisplayed = Boolean(options.isErrorTitleDisplayed);
      this.widgetOptions = options;

      this.header = this.createPaymentSetup.getHeader(InitiatePaymentHeaderStates.REVIEW);
    });

    this.paymentItem$ = this.stateService.form$.pipe(
      takeUntil(this.gc$),
      tap((form?: FormGroup) => (this.paymentForm = form)),
      map((form) => form?.getRawValue() || {}),
    );

    this.paymentItem$.pipe(takeUntil(this.gc$)).subscribe((data?: PaymentDetails) => {
      if (data && isObjectTruthy(data)) {
        this.paymentItem = data;
      } else {
        this.stateService.setPaymentState(PaymentState.INITIAL);
        this.navigationService.form();
      }
    });

    this.paymentTemplateForm = this.buildPaymentTemplateForm();
  }

  isSavingTemplate$(): BehaviorSubject<boolean> {
    return this.savingTemplate$;
  }

  private onSubmit(approved = false): void {
    this.submitting$.next(true);

    if (this.paymentItem.counterparty.saveNewContact) {
      this.saveNewContact();
    }

    this.postPaymentOrder(approved);
  }

  private saveNewContact(): void {
    this.paymentOrderHttpService
      .saveNewContact({
        ...contactItemToService(this.paymentItem.counterparty),
        accessContextScope: this.widgetOptions.accessContextScope,
      })
      .pipe(takeUntil(this.gc$))
      .subscribe((response: ContactsPostResponseBody | null) => {
        const { id, status } = response || {};
        this.saveContactResponse$.next(response);
        const contactSaved = !!id;
        this.paymentOrderHttpService.showNotification(contactSaved, {
          header: contactSaved
            ? status === ActiveContactStatus.PENDINGAPPROVAL
              ? reviewI18n.newContactSubmitSuccessPendingApproval
              : reviewI18n.newContactSubmitSuccess
            : reviewI18n.newContactSubmitFailure,
        });
      });
  }

  private handlePaymentError(error: PaymentError) {
    this.genericError = error?.error?.message;
    this.errors = error?.error?.errors;
    if (this.errors?.length || this.genericError) {
      const errorObject: PaymentErrors = {
        errorResponse: error,
        genericMessage: this.genericError,
        errors: this.errors,
        paymentErrors: this.paymentErrors,
        notificationHeader: reviewI18n.submitPaymentFailure,
      };
      this.paymentOrderHttpService.manageErrorResponse(errorObject);
    } else {
      this.paymentOrderHttpService.showNotification(false, {
        header: reviewI18n.submitPaymentFailureUnknownErrorHeader,
        message: reviewI18n.submitPaymentFailureUnknownError,
      });
    }
    const message = error?.statusText;
    let status: string;
    switch (message) {
      case 'rejected_by_user':
        status = ProgressStatus.USER_DECLINED;
        break;
      default:
        status = 'ERROR';
    }

    this.paymentStatus$.next(status);
    this.submitting$.next(false);
  }

  private putOrPostPaymentOrder(post: PaymentOrdersValidatePost): Observable<PaymentOrdersPostResponse> {
    const isCopyPayment = this.widgetOptions?.paymentMode === PaymentMode.COPY_PAYMENT;
    const paymentOrderId = this.paymentItem.paymentOrderId;
    return paymentOrderId && !isCopyPayment
      ? this.paymentOrderHttpService.putPaymentOrder({ ...post, id: paymentOrderId }, paymentOrderId)
      : this.paymentOrderHttpService.postPaymentOrder(post);
  }

  private postPaymentOrder(approved: boolean): void {
    const shouldApprove = approved ? { approved } : null;
    this.putOrPostPaymentOrder(toPostRequest({ ...this.paymentItem, ...shouldApprove }))
      .pipe(
        takeUntil(this.gc$),
        catchError((error: HttpErrorResponse) => {
          this.alertContainer?.nativeElement.scrollIntoView(false);

          if (this.hasLimitBreach(error)) {
            this.paymentStatus$.next('LIMIT_BREACH');
            this.breachError = error.error || error;
            this.submitting$.next(false);
          } else {
            this.handlePaymentError(error as PaymentError);
          }

          return EMPTY;
        }),
      )
      .subscribe((response: PaymentOrdersPostResponse | CapabilityData) => {
        if (this.transactionSigningHelper?.isTransactionSigningResponse(response)) {
          this.handleTransactionSigning(response as CapabilityData);
        } else {
          this.handlePaymentSubmit(response as PaymentOrdersPostResponse);
        }
      });
  }

  private handlePaymentSubmit(response: PaymentOrderResponseItem | Omit<PaymentOrderGetResponse, 'version'>): void {
    const { status: _paymentStatus } = response;
    const isApprovalRejected = _paymentStatus === ApprovalStatus.REJECTED;
    const status = !!response.id && !isApprovalRejected ? 'SUCCESS' : _paymentStatus || 'ERROR';

    this.paymentOrderId = response?.id;
    this.approvalPending = _paymentStatus === Status.ENTERED || _paymentStatus === ProgressStatus.PENDING_APPROVAL;

    if (!!response.id && !isApprovalRejected) {
      const isEditPayment = this.widgetOptions?.paymentMode === PaymentMode.EDIT_PAYMENT;
      const { submitSuccess, submitSuccessPendingApproval, editSubmitSuccess } = this.widgetOptions.isDebitPayment
        ? reviewI18n.debitPayment
        : reviewI18n.defaultPayment;

      const header = isEditPayment
        ? editSubmitSuccess
        : this.approvalPending
        ? submitSuccessPendingApproval
        : submitSuccess;

      this.paymentOrderHttpService.showNotification(true, { header });

      this.eventBus.publish(this.widgetOptions.successEventName || defaultPaymentSuccessEventName, true);
      this.header = this.createPaymentSetup.getHeader(InitiatePaymentHeaderStates.SUBMITTED);
    }

    this.paymentResponse = response;
    this.paymentStatus$.next(status);
    this.submitting$.next(false);
  }

  private handleTransactionSigning(response: CapabilityData): void {
    if (!this.transactionSigningHelper) {
      return;
    }
    const paymentOrderId = response['payment-order-id'];
    if (paymentOrderId) {
      this.transactionSigningHelper.getTransactionInfo({
        data: response,
        asyncPollingOptions: {
          pollInterval: this.widgetOptions.pollInterval,
          maxRetries: 3,
          pollUntilFn: this.isPaymentConfirmed,
          complete$: this.gc$,
        },
        fetchFn: this.getPaymentProgressStatus.bind(this),
        onSuccess: this.onTransactionSigningSuccess.bind(this, paymentOrderId),
        onRejected: this.onTransactionSigningReject.bind(this, paymentOrderId),
        onDeclined: this.onTransactionSigningDecline.bind(this),
        onError: this.onTransactionSigningError.bind(this),
      });
    }
  }

  private onTransactionSigningSuccess(paymentOrderId: string): void {
    this.paymentOrderHttpService
      .getPayment(paymentOrderId)
      .pipe(takeUntil(this.gc$))
      .subscribe(this.handlePaymentSubmit.bind(this));
  }

  private onTransactionSigningReject(paymentOrderId: string, _: TransactionCallbackParam): void {
    this.handleRejectedPayment(paymentOrderId);
  }

  private onTransactionSigningDecline({ status }: TransactionCallbackParam): void {
    const header = this.widgetOptions.isDebitPayment ? reviewI18n.submitDebitFailure : reviewI18n.submitPaymentFailure;

    this.paymentStatus$.next(status);
    this.submitting$.next(false);
    this.paymentOrderHttpService.showNotification(false, { header });
  }

  private onTransactionSigningError({ status }: TransactionCallbackParam): void {
    const header = this.widgetOptions.isDebitPayment ? reviewI18n.submitDebitFailure : reviewI18n.submitPaymentFailure;
    const errorMessage = status === 'COED' ? reviewI18n.submitPaymentApprovalFailureCOED : { header };

    this.submitting$.next(false);
    this.paymentStatus$.next(status === 'COED' ? 'COED' : 'ERROR');
    this.paymentOrderHttpService.showNotification(false, { ...errorMessage });
  }

  private handleRejectedPayment(paymentOrderId: string): void {
    this.paymentOrderHttpService
      .getPayment(paymentOrderId)
      .pipe(takeUntil(this.gc$))
      .subscribe((response) => {
        if (response.reasonCode === 'LTBR') {
          this.paymentStatus$.next('LIMIT_BREACH');
          this.breachError = {
            message: '',
            breachReport: response.breachReports,
          };
        } else {
          const header = this.widgetOptions.isDebitPayment
            ? reviewI18n.submitDebitFailure
            : reviewI18n.submitPaymentFailure;

          this.paymentOrderHttpService.showNotification(false, { header });
          this.paymentStatus$.next('ERROR');
        }

        this.submitting$.next(false);
      });
  }

  private getPaymentProgressStatus(data?: CapabilityData): Observable<string> {
    return this.paymentOrderHttpService.getPaymentProgressStatus(data?.['payment-order-id'] || '').pipe(
      map((res) => {
        if (
          res?.progressStatus === PaymentOrderProgressStatusGet.ProgressStatusEnum.REJECTED &&
          res.reasonCode === 'COED'
        ) {
          res = {
            ...res,
            progressStatus: res.reasonCode as PaymentOrderProgressStatusGet.ProgressStatusEnum,
          };
        }

        return res;
      }),
      pluck('progressStatus'),
    );
  }

  private isPaymentConfirmed(status: string): boolean {
    return !!status && status !== ProgressStatus.CONFIRMATION_PENDING && status !== ProgressStatus.SENDING;
  }

  private onHooksDone(approved: boolean, success = true): void {
    if (success) {
      this.onSubmit(approved);
    } else {
      this.submitting$.next(false);
    }
  }

  private hasLimitBreach(response: any): boolean {
    const isP2PLimitBreach = Object.values(P2PLimitReasonCodes).includes(response.reasonCode);
    return !!response?.breachReport || !!response?.error?.breachReport || isP2PLimitBreach;
  }

  showBreachModal(): void {
    this.showBreachDetails = true;
  }

  getPaymentResponse(): Partial<PaymentOrderResponseItem> | undefined {
    return this.paymentResponse;
  }

  hideBreachModal(): void {
    this.showBreachDetails = false;
  }

  isTemplateSaved(): boolean {
    return this.saveTemplate;
  }

  private buildPaymentTemplateForm(): FormGroup {
    return this.formBuilder.group({
      templateName: ['', [Validators.required, noWhitespaceValidator]],
    });
  }

  openTemplateModal(): void {
    this.paymentTemplateModalState.next(ModalState.Preview);
  }

  closeTemplateModal(): void {
    this.paymentTemplateModalState.next(ModalState.Hidden);
    this.paymentTemplateForm.reset();
  }

  submitPaymentTemplate(templateName = ''): void {
    if (this.paymentOrderId) {
      const paymentOrderId = this.paymentOrderId;
      const name = templateName || this.paymentTemplateForm.controls['templateName'].value;
      const templateObject: PaymentTemplatesPostRequest = {
        paymentOrderId,
        name,
      };

      if (this.paymentTemplateForm.invalid) {
        return;
      }

      this.savingTemplate$.next(true);

      this.paymentOrderHttpService
        .postPaymentTemplate(templateObject)
        .pipe(takeUntil(this.gc$))
        .subscribe(
          () => {
            this.saveTemplate = true;
            this.savingTemplate$.next(false);
            this.paymentOrderHttpService.showNotification(true, {
              header: commonI18n.templateSaveSuccess(name),
              headerContext: { name },
            });
            this.paymentTemplateForm.reset();
            this.paymentTemplateModalState.next(ModalState.Hidden);
            this.eventBus.publish(
              <string>(this.widgetOptions.successEventName || defaultPaymentSuccessEventName),
              true,
            );
          },
          () => {
            this.paymentOrderHttpService.showNotification(false, {
              header: commonI18n.templateSaveFailed,
            });
            this.savingTemplate$.next(false);
          },
        );
    }
  }

  submitPayment(approved = false): void {
    const hasHooks = this.createPaymentSetup.hooks && this.createPaymentSetup.hooks[PaymentHooks.onSubmit];
    this.submitting$.next(true);
    if (hasHooks) {
      triggerPaymentHook(
        <PaymentHooksOptions>this.createPaymentSetup.hooks,
        PaymentHooks.onSubmit,
        this.onHooksDone.bind(this, approved),
        this.paymentForm,
      );
    } else {
      this.onSubmit(approved);
    }
  }

  getForexDetails(payment: PaymentDetails): Partial<ForexDetails> | undefined {
    return getForexDetailsFromPayment(payment);
  }

  isApprovalEnabled(): boolean {
    return !!this.widgetOptions.enableApprovals && this.canApprove;
  }

  isPaymentTemplateEnabled(): boolean {
    return !!this.widgetOptions.enableSavePaymentAsTemplate && this.enablePaymentTemplate;
  }

  isNewPaymentEnabled() {
    return this.widgetOptions.enableNewPaymentButton !== false;
  }

  discardPayment() {
    if (this.widgetOptions?.isModalView) {
      this.modalService?.closeModal();
    } else {
      this.resetPayment();
    }
  }

  resetPayment(): void {
    this.showConfirmation = false;
    this.widgetOptions.paymentMode = PaymentMode.CREATE_PAYMENT;
    this.stateService.resetForm();
    this.stateService.setPaymentState(PaymentState.INITIAL);
    this.navigationService.form();
  }

  toggleConfirmationDialog(): void {
    this.showConfirmation = !this.showConfirmation;
  }

  showDiscardDialog(): boolean {
    return this.showConfirmation;
  }

  isApprovalPending(): boolean {
    return this.approvalPending;
  }

  isSubmitting$(): BehaviorSubject<boolean> {
    return this.submitting$;
  }

  getPaymentStatusType(): PaymentSubmitStatuses {
    return this.widgetOptions?.paymentMode === PaymentMode.EDIT_PAYMENT
      ? PaymentSubmitStatuses.PENDING_EDITED
      : this.isApprovalPending()
      ? PaymentSubmitStatuses.PENDING
      : PaymentSubmitStatuses.SUCCESS;
  }

  getPaymentMessage(): string | undefined {
    return this.paymentMessage;
  }

  getPurposeDescription(purpose: string): string | undefined {
    const purposes = this.paymentItem.inputFormFromSettings?.purposesOfPayment;
    return purposes?.length ? purposes.find((item: PurposeOfPayment) => item.code === purpose)?.description : '';
  }

  doneEditing(): void {
    this.modalService?.closeModal();
  }

  navigateToAfterSuccessPage(): void {
    this.navigationService.afterSuccess();
  }

  ngOnDestroy(): void {
    this.gc$.next();
    this.gc$.complete();
  }
}
