import { Injectable, TemplateRef } from '@angular/core';
import {
  FetchPaymentOptionsListenerFn,
  InitiatePaymentHeaderStates,
  InitiatePaymentOptions,
  PaymentFormFieldConfig,
  PaymentFormGroup,
  PaymentHooksOptions,
  PaymentTypeConfig,
  ReviewTemplateContext,
} from '@backbase/payment-orders-ang';
import { BehaviorSubject } from 'rxjs';
import { BaseFields } from '../constants/initiate-payment.const';

@Injectable()
export class CreatePaymentConfigSetupService {
  private readonly configsSub$ = new BehaviorSubject<PaymentTypeConfig[]>([]);
  private readonly optionsSub$ = new BehaviorSubject<InitiatePaymentOptions>(
    {}
  );
  private readonly baseFieldsSub$ = new BehaviorSubject<
    Array<PaymentFormFieldConfig | PaymentFormGroup>
  >([]);
  private readonly fetchPaymentOptionsListenerSub$ = new BehaviorSubject<
    FetchPaymentOptionsListenerFn | undefined
  >(undefined);
  private readonly hooksSub$ = new BehaviorSubject<PaymentHooksOptions>({});
  private readonly reviewTemplateSub$ = new BehaviorSubject<
    TemplateRef<ReviewTemplateContext> | undefined
  >(undefined);

  readonly configs$ = this.configsSub$.asObservable();
  readonly options$ = this.optionsSub$.asObservable();
  readonly baseFields$ = this.baseFieldsSub$.asObservable();
  readonly fetchPaymentOptionsListener$ =
    this.fetchPaymentOptionsListenerSub$.asObservable();
  readonly reviewTemplate$ = this.reviewTemplateSub$.asObservable();

  get hooks(): PaymentHooksOptions {
    return this.hooksSub$.value;
  }

  get options(): InitiatePaymentOptions {
    return this.optionsSub$.value;
  }

  get configs(): PaymentTypeConfig[] {
    return this.configsSub$.value;
  }

  setHooks(hooks: PaymentHooksOptions = {}) {
    this.hooksSub$.next(hooks);
  }

  setConfigs(configs: PaymentTypeConfig[]) {
    this.configsSub$.next(configs);
  }

  setOptions(options: InitiatePaymentOptions) {
    this.optionsSub$.next(options);
  }

  setBaseFields(
    baseFields: Array<PaymentFormFieldConfig | PaymentFormGroup> = BaseFields
  ) {
    this.baseFieldsSub$.next(baseFields);
  }

  setFetchPaymentOptionsListener(listenerFn?: FetchPaymentOptionsListenerFn) {
    this.fetchPaymentOptionsListenerSub$.next(listenerFn);
  }

  setReviewTemplate(templateRef: TemplateRef<ReviewTemplateContext>) {
    this.reviewTemplateSub$.next(templateRef);
  }

  getHeader(state: InitiatePaymentHeaderStates): string | undefined {
    const paymentType =
      this.configs.length === 1 ? this.configs[0].paymentType : '';
    const data = {
      paymentMode: this.options?.paymentMode,
      state,
      paymentType,
      isDebit: this.options.isDebitPayment,
    };
    return this.options.header?.(data);
  }
}
