import {
  PaymentOptionsRequest,
  SchemeName,
} from '@backbase/data-ang/payment-order-options';
import { PaymentTemplate } from '@backbase/data-ang/payment-template';
import { PaymentTypes } from '../constants/initiate-payment.const';
import { FormGroup } from '@angular/forms';
import {
  CounterPartyFields,
  getConfig,
  getCounterpartySchemeName,
  isAddressTruthy,
  PaymentBaseFields,
  PaymentDetails,
  PaymentFormGroup,
  PaymentTypeConfig,
  stripSpaces,
} from '@backbase/payment-orders-ang';

export const mapPaymentOptionRequest = (
  formItem: PaymentDetails,
  clientServiceAgreementID = '',
  clientJurisdiction = ''
): PaymentOptionsRequest => {
  const { creditorBank } = formItem.counterparty;
  const counterpartyCountry =
    creditorBank &&
    creditorBank.postalAddress &&
    creditorBank.postalAddress.country;
  const bankBranchCode = creditorBank && creditorBank.bankBranchCode;
  const creditorScheme = getCounterpartySchemeName(formItem);
  return {
    ...(clientServiceAgreementID ? { clientServiceAgreementID } : null),
    ...(clientJurisdiction ? { clientJurisdiction } : null),
    originatorAccount: {
      identification: formItem.initiator.id,
      schemeName: SchemeName.ID,
    },
    counterpartyAccount: {
      ...(bankBranchCode ? { bankBranchCode } : null),
      identification:
        creditorScheme === SchemeName.ID
          ? stripSpaces(formItem.counterparty.id)
          : stripSpaces(formItem.counterparty.accountNumber),
      schemeName: <SchemeName>creditorScheme,
    },
    requestedExecutionDate: new Date().toISOString().substring(0, 10),
    instructedAmount: {
      amount: formItem.remittanceInfo.amountGroup.amount,
      currencyCode: formItem.remittanceInfo.amountGroup.currency,
    },
    ...(counterpartyCountry ? { counterpartyCountry } : null),
    additions: formItem.additions,
  };
};

export function normalizePaymentType(
  selectedPaymentItem: PaymentTemplate | undefined
): string | undefined {
  let _selectedPayment = selectedPaymentItem?.details.paymentType;
  if (
    _selectedPayment ===
      `${PaymentTypes.SEPA_PREFIX}${PaymentTypes.CLOSED_POSTFIX}` ||
    _selectedPayment ===
      `${PaymentTypes.SEPA_PREFIX}${PaymentTypes.ILE_POSTFIX}`
  ) {
    _selectedPayment = _selectedPayment.replace(
      PaymentTypes.SEPA_PREFIX,
      PaymentTypes.SEPA
    );
  }
  let replacedPayment;
  if (_selectedPayment) {
    if (_selectedPayment.endsWith(PaymentTypes.ILE_POSTFIX)) {
      replacedPayment = _selectedPayment.replace(PaymentTypes.ILE_POSTFIX, '');
    } else if (_selectedPayment.endsWith(PaymentTypes.CLOSED_POSTFIX)) {
      replacedPayment = _selectedPayment.replace(
        PaymentTypes.CLOSED_POSTFIX,
        ''
      );
    } else {
      replacedPayment = _selectedPayment;
    }
  }

  return replacedPayment;
}

export function normalisePaymentTemplate(paymentTemplate: PaymentTemplate) {
  return {
    ...paymentTemplate,
    details: {
      ...paymentTemplate.details,
      paymentType: normalizePaymentType(paymentTemplate),
    },
  };
}

/**
 * Reset beneficiary address if the form contains address details but the selected config does not contain address config.
 */
export function resetBeneficiaryAddress(
  paymentTypeConfig: PaymentTypeConfig,
  form: FormGroup
) {
  const counterPartyConfig = getConfig(
    PaymentBaseFields.counterparty,
    paymentTypeConfig
  ) as PaymentFormGroup;
  const beneficiaryAddress = getConfig(
    CounterPartyFields.postalAddress,
    counterPartyConfig
  );
  const beneficiaryAddressGroup = form.get([
    PaymentBaseFields.counterparty,
    CounterPartyFields.postalAddress,
  ]);
  if (
    !beneficiaryAddress &&
    beneficiaryAddressGroup &&
    isAddressTruthy(beneficiaryAddressGroup.value)
  ) {
    beneficiaryAddressGroup.reset();
  }
}

/**
 * Reset bank details if the form contains bank details but the selected config does not contain specific bank detail config.
 */
export function resetBankDetails(
  paymentTypeConfig: PaymentTypeConfig,
  form: FormGroup
) {
  const counterPartyConfig = getConfig(
    PaymentBaseFields.counterparty,
    paymentTypeConfig
  ) as PaymentFormGroup;
  const beneficiaryBankConfig = getConfig(
    CounterPartyFields.creditorBank,
    counterPartyConfig
  ) as PaymentFormGroup;
  const beneficiaryBankAddress = beneficiaryBankConfig
    ? getConfig(CounterPartyFields.postalAddress, beneficiaryBankConfig)
    : null;
  const beneficiaryBankAddressGroup = form.get([
    PaymentBaseFields.counterparty,
    CounterPartyFields.creditorBank,
    CounterPartyFields.postalAddress,
  ]);

  resetBankField(CounterPartyFields.bic, counterPartyConfig, form);
  resetBankField(CounterPartyFields.bankName, counterPartyConfig, form);

  if (
    !beneficiaryBankAddress &&
    beneficiaryBankAddressGroup &&
    isAddressTruthy(beneficiaryBankAddressGroup.value)
  ) {
    for (const key in (<FormGroup>beneficiaryBankAddressGroup).controls) {
      if (key !== 'country') {
        (<FormGroup>beneficiaryBankAddressGroup).controls[key].reset();
      }
    }
  }
}

export function resetBankField(
  fieldName: string,
  counterpartyConfig: PaymentFormGroup,
  form: FormGroup
) {
  const config = getConfig(fieldName, counterpartyConfig);
  const control = form.get([
    PaymentBaseFields.counterparty,
    CounterPartyFields.creditorBank,
    fieldName,
  ]);
  if (!config && control?.value) {
    control.reset();
  }
}
